.dice {
    float: left;
    position: relative;
    width: 50%;
    padding-bottom: 50%; /* = width for a 1:1 aspect ratio */
    margin: 2.66%;
    overflow: hidden;
    background-color: navy;
    border-radius: 4px;
}

.dice-face {
    position: absolute;
    width: 100%;
    height: 100%;
    color: white;
    text-align: center;
    font-size: 300%;
}

.dice-face-table {
	display:table;
    height:100%;
    width:100%;
}

.dice-face-table-cell{
    display:table-cell;
    vertical-align:middle;
    height:100%;
    width:100%;
}

.point-buy-display {
    text-align: center;
    font-size: 300%;
}